  .Rsubmit-button{
    position: relative;
    padding: 10px 30px;
  background-image: url("data:image/jpeg;base64,");
  color: aliceblue;
  font-size: 21px;
  top:26px;
  left:-90px;
  border: none;
  cursor: pointer;
  }
  .Rsubmit-button:hover{
    background-image: url("data:image/jpeg;base64,");
  color: crimson;
  border-color: black;
  border: 20px;
  }
  .Rreset-button{
    position: relative;
    border: none;
    background-color: aliceblue;
    font-size: 20px;
  }
  .divi-r{
    position: relative;
    left:80px;
  
  }
  .Rreset-button:hover{
    position: relative;
    border: none;
    background-color: aliceblue;
   
    
    font-size: 20px;
  color: brown;
  cursor: pointer;
  }
  .reg-img{
    position: relative;
    width: 10%;
   
  }
  .register-bg-image{
    position: absolute;
    width: 12%;
    top:400px;
    left:200px;
    animation-name: vanakam;
    animation-duration: 2s;
  }
  @keyframes vanakam{
     from{
      left:-130px;
     }
     to{
      left:200px;
     }
  }
  .tnd-vanakam1{
    position: relative;
    font-size: 40px;
    top:200px;
    left:190px;
    animation-name: head1-vanakam;
    animation-duration: 2s;
    color: rgb(15, 15, 88);
    font-style: italic;
    font-weight: bold;
  }
  @keyframes head1-vanakam {
    from{
      left:-70px
    }
    to{
      left:190px;
     }
  }
  .tnd-vanakam2{
    position: relative;
    font-size: 40px;
    top:200px;
    left:160px;
    animation-name: head2-vanakam;
    animation-duration: 2s;
    color: rgb(136, 26, 99);
    font-style: italic;
    font-weight: bold;
  }
  @keyframes head2-vanakam {
    from{
      left:-70px
    }
    to{
      left:160px;
     }
  }
  /*------------responsive code---------*/
  @media (min-width:300px) and (max-width:500px){
    body{
      overflow-x: hidden;
    }
    .register-head{
      position: relative;
      text-align: center;
      top:30px;
      left:10px;
      color: rgb(202, 32, 100);
      font-size: 30px;
      font-family: 'Outfit', sans-serif;
    }
    .form-division{
