.form-division-login{
    position: relative;
    justify-content: center;
    text-align: center;
    top:-70px;
    left:360px;
}

.login-head{
    position: relative;
  text-align: center;
  top:-80px;
  left:360px;
  color: rgb(202, 32, 100);
  font-size: 70px;
  font-family: 'Outfit', sans-serif;
}

.Linput-field-email{
    position: relative;
    background-image: url("data:image/png;base64,");
   width: 500px;
   height: 60px;
   font-size: 20px;
   left:500px;
top:100px;
    border-radius: 50px;
    background-color: rgb(243, 229, 240);
    border: none;
    padding: 16px 50px 16px 70px;
    background-repeat: no-repeat;
    background-size: 60px;
}
.Linput-field-password{
    position: relative;
    background-image: url("https://p7.hiclipart.com/preview/818/360/1007/computer-icons-password-login-user-padlock.jpg");
   width: 500px;
   height: 60px;
   left:500px;
   font-size: 20px;
  top:90px;
    border-radius: 50px;
    background-color: rgb(243, 229, 240);
    border: none;
    padding: 16px 50px 16px 70px;
    background-repeat: no-repeat;
    background-size: 60px;
}
.Lsubmit-button{
    position: relative;
  padding: 10px 30px;
background-image: url("data:image/jpeg;base64,");
color: aliceblue;
font-size: 21px;
top:100px;
left:-90px;
border: none;
cursor: pointer;
}
.Lsubmit-button:hover{
    background-image: url("data:image/jpeg;base64,");
  color: crimson;
  border-color: black;
  border: 20px;
  }
  .Lreset-button{
    position: relative;
    border: none;
    background-color: aliceblue;
    top:100px;
    font-size: 20px;
    left:150px;
  }
  .Lreset-button:hover{
    position: relative;
    border: none;
    background-color: aliceblue;
    top:30px;
    font-size: 20px;
  color: brown;
  cursor: pointer;
  }
  .reset-division-login{
    position: relative;
    top:-30px;
    left:-30px;
  }
  .t-im1{
    position: relative;
    left:10px;
    animation-name: l-img-0;
    animation-duration: 3s;
    border-radius: 50%;
  }
  @keyframes l-img-0 {
    from{
      left:-300px
    }
    to{
      left:10px;
    }
  }
  .t-im2{
    position: relative;
    top:190px;
    left:10px;
    animation-name: l-img-1;
    animation-duration: 3s;
    border-radius: 50%;

  }
  @keyframes l-img-1 {
    from{
      left:-300px
    }
    to{
      left:10px;
    }
  }
  .t-im3{
    position: relative;
    top:370px;
    left:-100px;
    border-radius: 50%;

    animation-name: l-img-2;
    animation-duration: 3.7s;
  }
  @keyframes l-img-2 {
    from{
      left:-300px
    }
    to{
      left:-100px;
    }
  }
  .t-im4{
    position: relative;
    top:550px;
    left:-570px;
    border-radius: 50%;

    animation-name: l-img-3;
    animation-duration: 3s;
  }
  @keyframes l-img-3 {
    from{
      left:-1100px
    }
    to{
      left:-570px;
    }
  }
  .t-im5{
    position: relative;
    top:330px;
    left:-1160px;
    border-radius: 50%;

    animation-name: l-img-4;
    animation-duration: 3s;
  }
  @keyframes l-img-4 {
    from{
      left:-1500px
    }
    to{
      left:-1160px;
    }
  }
/*-------------responsive code----------*/
@media (min-width:300px) and(max-width:800px){
  
  body{
    overflow-x: hidden;
  }
  .login-head{
    position: relative;
    text-align: center;
    top:-4000px;
    left:10px;
    color: rgb(202, 32, 100);
    font-size: 30px;
    font-family: 'Outfit', sans-serif;
  }
  .form-division-login{
    position: relative;
    justify-content: center;
    text-align: center;
    top:-40px;
    left:-40px;
  }
  .t-im2{
    position: relative;
    top:100px;
    left:10px;
    animation-name: l-img-1;
    animation-duration: 3s;
    border-radius: 50%;

  }
  @keyframes l-img-1 {
    from{
      left:-300px
    }
    to{
      left:10px;
    }
  }
  .t-im3{
    top:60px;
    left:10px;
  }
 
}
@media (min-width:300px) and (max-width:600px) {
  .t-im1{
          display: none;
  }
  .t-im2{
    display: none;
  }
  .t-im3{
    display: none;
  }
  .t-im4{
    display: none;
  }
  .t-im5{
    display: none;
  }
  .form-division-login{
    top:500px;
    left:90px;
  }
  .Linput-field-email{
    left:-30px;
  }
  .Linput-field-password{
    left:-30px;
  }
  .login-head{
    top:400px;
    left:120px;
  }
}